<template>
  <div class="container">
      <Header />
      <div class="content">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
          <!--  -->
          <van-swipe :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item, index) of bannerlist" :key="index">
            <img :src="item" alt="">
          </van-swipe-item>
          </van-swipe>
          <!--  -->
          <div class="content_kind">
            <div class="kind_1">
              <img src="../../../public/img/tb1.png" alt="" @click="goRanking('', '')">
              <span></span>
            </div>
            <div class="kind_1">
              <img src="../../../public/img/tb2.png" alt="" @click="goRanking('', '')">
              <span></span>
            </div>
            <div class="kind_1">
              <img src="../../../public/img/tb3.png" alt="" @click="goRanking('', '9.9')">
              <span>9.9</span>
            </div>
            <div class="kind_1">
              <img src="../../../public/img/tb5.png" alt="" @click="goRanking('', '')">
              <span></span>
            </div>
            <div class="kind_1">
              <img src="../../../public/img/tb6.png" alt="" @click="goRanking('', '')">
              <span></span>
            </div>
          </div>
          <div class="empty"></div>
          <img class="tip" src="../../../public/img/tip1.jpg" alt="">
          <!--  -->
          <div class="new">
            <div class="new_top">
              <div class="new_img">
                <img src="../../../public/img/newbook.png" alt="">
              </div>
              <div class="new_check" @click="add">
                <van-icon name="replay" />
                <span></span>
              </div>
            </div>
            <div class="new_item">
              <Detaillist :detaillist='detaillist'/>
            </div>
          </div>
          <!--  -->
          <div class="recommend"></div>
          <!--  -->
          <div class="prolist_title"></div>
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text=""
            @load="onLoad"
          >
            <Prolist :prolist="prolist" />
          </van-list>
        </van-pull-refresh>
      </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Search, Swipe, SwipeItem, Grid, GridItem, List, PullRefresh, Icon } from 'vant'
import Prolist from '@/components/common/Prolist'
import Header from '@/components/common/Header'
import Detaillist from '@/components/common/Detaillist'

Vue.use(Search)
Vue.use(Swipe).use(SwipeItem)
Vue.use(Grid).use(GridItem)
Vue.use(List)
Vue.use(PullRefresh)
Vue.use(Icon)
export default {
  data () {
    return {
      bannerlist: [],
      prolist: [],
      detaillist: [],
      loading: false,
      finished: false,
      pageNum: 1,
      isLoading: false,
      flag: false,
      num: 1
    }
  },
  components: {
    Prolist,
    Detaillist,
    Header
  },
  methods: {
    onRefresh () {
      this.isLoading = true
      //fetch('/api/book/find').then(res => res.json()).then(data => {
      fetch('http://47.100.225.183:8090/book/find').then(res => res.json()).then(data => {
        this.isLoading = false // 
        this.prolist = data // 
        this.pageNum = 1 //  --- 
        this.finished = false // 
      })
    },
    onLoad () {
      console.log('')
      this.loading = true // 
      //fetch('/api/book/find?count=20&start=' + this.pageNum * 20)
      fetch('http://47.100.225.183:8090/book/find?count=20&start=' + this.pageNum * 20)
        .then(res => res.json()).then(data => {
          this.loading = false // 
          this.pageNum++
          if (data.length === 0) {
            console.log('')
            this.finished = true // 
          } else {
            //    concat
            this.prolist = [...this.prolist, ...data.data]
          }
        })
    },
    goRanking (id, msg) {
      this.$router.push({ name: 'rankinglist', params: { id: id, msg: msg } })
    },
    add () {
      this.num++
      //fetch('/api/book/find?count=4&start=' + this.num * 4, { method: 'GET' })
       fetch('http://47.100.225.183:8090/book/find?count=4&start=' + this.num * 4, { method: 'GET' })
        .then(res => res.json()).then(data => {
          this.detaillist = data.data
        })
    }
  },
  mounted () {
    //fetch('/api/banner')
    fetch('http://47.100.225.183:8090/banner')
      .then(res => res.json())
      .then(data => {
        console.log(data.data)
        var dataD = data.data
        var arr = []
        dataD.map(item => {
          item = item.bannerImg
          arr.push(item)
        })
        this.bannerlist = arr
      })
    //fetch('/api/book/find', { method: 'GET' })
    fetch('http://47.100.225.183:8090/book/find', { method: 'GET' })
      .then(res => res.json())
      .then(data => {
        console.log(data.data)
        this.prolist = data.data
      })
    //fetch('/api/book/find?count=4&start=' + this.num * 4, { method: 'GET' })
    fetch('http://47.100.225.183:8090/book/find?count=4&start=' + this.num * 4, { method: 'GET' })
      .then(res => res.json()).then(data => {
        this.detaillist = data.data
      })
  }
}
</script>

<style lang="scss" scoped>
@import '@/lib/reset.scss';

.van-swipe {
  height: 1.55rem;
  img {
    width: 100%;
  }
}
.prolist_title {
    @include padding(0.15rem 0 0 0.1rem);
    @include font-size(0.22rem);
    @include font-weight(500);
  }
.content_kind {
  @include flexbox();
  @include rect(100%, 0.8rem);
  @include border(0 0 1px 0, #efefef, solid);
  @include padding(0 0 10px 0);
  .kind_1 {
    @include flex();
    @include flexbox();
    @include flex-direction(column);
    @include justify-content();
    @include align-items();
    @include font-size(0.16rem);
    img {
      @include rect(auto, 0.45rem);
    }
    span {
      font-size: 14px;
    }
  }
}
.tip {
  @include rect(100%, auto);
  @include padding(0.05rem);
}
.empty {
  @include rect(100%, 0.1rem);
  @include background-color(#efefef)
}
.new {
  .new_top {
    @include padding(0.15rem 0);
    @include flexbox();
    @include rect(100%, 0.4rem);
    @include border(1px 0, #dddddd, solid);
    @include align-items();
    .new_img {
      @include flex(3.5);
      img {
      @include rect(1.4rem, auto);
      }
    }
    .new_check {
      @include flex(1);
      @include color(#333);
      @include font-size(0.16rem);
    }
  }
}
</style>
